import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Form,
  Tabs,
  Table,
  Button,
  Modal,
  Row,
  Col,
  message,
  Input,
  Tooltip,
  Popconfirm,
} from 'antd';

import { injectIntl } from 'react-intl';
import { getActors, addActor, deleteActor } from '../../services';

const { TabPane } = Tabs;
const FormItem = Form.Item;

const ActorsCmp = props => {
  const {
    form,
    form: { getFieldDecorator },
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(true);
  const [actors, setActors] = useState([]);
  const [actor, setActor] = useState(null);
  const [mode, setMode] = useState('add');

  useEffect(() => {
    fetch();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetch = async () => {
    try {
      setLoading(true);
      const { data } = await getActors();
      setActors(data.actors);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  const remove = async record => {
    try {
      setLoading(true);
      await deleteActor(record.id);
      fetch();
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          name: values.name,
          email: values.email,
        };
        setLoading(true);
        try {
          await addActor(form);
          message.success(messages.default.success);
          handleReset();
          setVisible(false);
          fetch();
        } catch (error) {
          setVisible(true);
          setLoading(false);
          message.error(error.message);
        }
      }
    });
  };

  const showModal = value => {
    handleReset();
    setMode(value);
    setVisible(true);
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      render: (value, record) => {
        return (
          <React.Fragment>
            <Tooltip placement="top" title="Edit Actor">
              <Button
                style={{ marginRight: 5 }}
                onClick={() => {
                  setActor(record);
                  showModal('edit');
                }}
                size="small"
                type="primary"
                icon="edit"
              />
            </Tooltip>
            <Popconfirm
              onConfirm={() => {
                remove(record);
              }}
              title="Are you sure?"
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <Tooltip placement="top" title="Delete Actor">
                <Button size="small" type="danger" icon="delete" />
              </Tooltip>
            </Popconfirm>
          </React.Fragment>
        );
      },
    },
  ];

  const operations = (
    <React.Fragment>
      <Button
        loading={loading}
        onClick={() => {
          showModal('add');
        }}
        type="primary"
        icon="smile"
      >
        Add
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  return (
    <div>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab="Actors" key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={actors}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title="Add Actor"
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label="Name" hasFeedback>
                  {getFieldDecorator('name', {
                    initialValue: actor && actor.name ? actor.name : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.node_id.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label="Email" hasFeedback>
                  {getFieldDecorator('email', {
                    initialValue: actor && actor.email ? actor.email : '',
                    rules: [
                      {
                        type: 'email',
                        message: 'The input is not valid E-mail!',
                      },
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedActorsCmp = Form.create()(injectIntl(ActorsCmp));

export default withRouter(WrappedActorsCmp);
